    //   }
    // }
  },
  components: { sidebarItem,},
  computed: {
    routes() {
      console.log(global.antRouter)
      return global.antRouter
    }
  }
}
</script>
<style>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  height: 100%;
}
</style>


