<template>
  <div :style="this.$store.state.allCollapseState === false ? {width:'100%',marginLeft:'200px',height:'100%',display: 'flex',flexDirection: 'column'} : {width:'100%',marginLeft:'65px',height:'100%',display: 'flex',flexDirection: 'column'}">
    <!-- changeforstate :{{this.$store.state.allCollapseState}} -->
    <div style="height:60px;display:flex">
      <i v-show="this.$store.state.allCollapseState" class="el-icon-s-unfold" style="margin:15px" @click="teston($store.state.allCollapseState)"></i>
      <i v-show="!this.$store.state.allCollapseState" class="el-icon-s-fold" style="margin:15px" @click="teston($store.state.allCollapseState)"></i>
      <!-- separator属性 -->
      <el-breadcrumb separator-class="el-icon-arrow-right" style="line-height:4">
        <el-breadcrumb-item :to="{ path: '/dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item v-for="(item, index) in breadList" :key="index">
          {{item.meta.title}}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container style="display:'flex';border-left: solid 1px #e6e6e6;">
      <el-main :style="defaultHeight">
        <section>
          <transition name="fade" mode="out-in">
            <router-view></router-view>
          </transition>
        </section>
      </el-main>
      <!-- 底栏 -->
      <el-footer height="60px" style="font-size: 14px;color: #B3C0D1;">Copyright &copy; 2021 LSG All Rights Reserved.</el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'AppMain',
  data() {
    return {
        defaultHeight: {
            height: ""
        },
        breadList:"",
    };
  },
  props: {
    changeforstate:{
      type:Boolean |String,
    },
    // routes: {
    //   type: Array,
    // },
  },
  watch:{
    $route() {
      this.getBreadcrumb()
    }
  },
  methods: {
    teston(data){
      // console.log(this.$store.state.allCollapseState)
      if(data){
        this.$store.state.allCollapseState = Boolean(0) 
      }else{
        this.$store.state.allCollapseState = Boolean(100)
      }
    },
    isHome(route) {
      // console.log("title:::::::"+route.meta.title)
      let test = route.meta.title === "首页"
      return test;
    },
    isHexName(route,routesec){
      let fir = route.meta.title === routesec.meta.title
      if(fir){

      }
      return fir;
    },
    getBreadcrumb() {
      // console.log(this.$route.matched.filter(item => item))  //可以获取上下文路由 也就是可以获取父亲和孩子路由组成的数组
      let matched = this.$route.matched;
      // console.log(matched);
      if (Object.keys(this.$route.matched[0].meta).length > 0) {
        //如果不是首页默认输出路由
        if (!this.isHome(matched[0])) {
          // matched = [{ path: "/dashboard", meta: { title: "首页" } }].concat(matched);
          //如果上级路由与下级路由相同，则只输出一个路径
          if(this.isHexName(matched[0],matched[1])){
            matched = [{ path: matched[0].path, meta: { title: matched[0].meta.title } }];
          }
        }else{
          matched = "";
        }
        this.breadList = matched;
      } else {
        this.breadList = [];
      }
    }
  },
  computed: {
    routes() {
      // console.log(global.antRouter)
      return global.antRouter
    },
  }
}
</script>
<style>
.el-header, .el-footer {
    /* background-color: #B3C0D1; */
    color: #333;
    text-align: center;
    line-height: 60px;
}
/* 图标大小 */
.el-icon-s-fold{
  font-size: 1.8rem;
}
.el-icon-s-unfold{
  font-size: 1.8rem;
}

/* .el-main {
  height: calc(100vh - (80px + 70px + 60px));
} */
</style>


