import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
//实例化store
const store = new Vuex.Store({
//这里类似data,保存全局变量
    state: {
        allCollapseState: false,
        // username:""
    },
    //这里类似methods，修改state里面变量的值需要调用此方法
    mutations: {
    //这里方法里面的state是系统自动传入的，不用手动传入。后面的变量需要调用方法的时候手动传入
        allCollapseState (state, allCollapseState) {
            state.allCollapseState = allCollapseState;
        }
    },
    //这里是用来调用mutations里面的方法，因为mutation不能直接被调用
    actions:{
        changeCollapse (state, allCollapseState) {
            state.allCollapseState = allCollapseState;
        }
    }
});
export default store;


