<template>
  <div class="app-wrapper">
    <el-container style="height:100%">
      <el-aside :style="this.$store.state.allCollapseState === false ? {width:'210px',position: 'absolute'} : {width:'auto',position: 'absolute'}">
        <sidebar class="sidebar-container"></sidebar>
      </el-aside>
      <el-container>
        <app-main></app-main>
      </el-container>
    </el-container>
    <!-- <div class="main-container">
      <app-main></app-main>
    </div> -->
  </div>
</template>

<script>
import AppMain from "@/views/layout/components/AppMain"; //页面布局的右侧区域
import sidebar from "@/views/layout/components/sidebar"; //页面布局的左侧菜单

export default {
  name: "layout",
  method: {},
  components: {
    sidebar,
    AppMain,
  },
};
</script>

<style scoped>
.app-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}
</style>



