<template>
  <el-container class="Site">
    <el-header style="height: 90px;display: flex;">
      <div style="flex:1;height:0;text-align: left;">
        <img src="././assets/logo.png" style="height:90px;"/>
      </div>
      <div style="flex:1;height:0;margin-top:15px;">
        <el-input placeholder="全局搜索" prefix-icon="el-icon-search" v-model="input" clearable></el-input>
      </div>
      <el-dropdown style="margin-top: 15px;flex: 1;text-align: end;">
        <span class="el-dropdown-link">
          <el-avatar :size="60" :src="circleUrl"></el-avatar>
        </span>
        <el-dropdown-menu slot="dropdown">
          <router-link :to="`dashboard`" style="text-decoration:none">
            <el-dropdown-item>个人中心</el-dropdown-item>
          </router-link>
          <router-link :to="`dashboard`" style="text-decoration:none">
            <el-dropdown-item divided>退出登录</el-dropdown-item >
          </router-link>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!-- 嵌套容器 -->
    <el-container>
      <div id="app">
        <router-view />
      </div>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      breadList: "",
      input:'',
    };
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    },
  },
  methods: {
    teston(data) {
      // console.log(this.$store.state.allCollapseState)
      if (data) {
        this.$store.state.allCollapseState = Boolean(0);
      } else {
        this.$store.state.allCollapseState = Boolean(100);
      }
    },
    isHome(route) {
      // console.log("title:::::::"+route.meta.title)
      let test = route.meta.title === "首页";
      return test;
    },
    getBreadcrumb() {
      // console.log(this.$route.matched.filter(item => item))  //可以获取上下文路由 也就是可以获取父亲和孩子路由组成的数组
      let matched = this.$route.matched;
      if (Object.keys(this.$route.matched[0].meta).length > 0) {
        //如果不是首页默认输出路由
        if (!this.isHome(matched[0])) {
          // matched = [{ path: "/dashboard", meta: { title: "首页" } }].concat(matched);
        } else {
          matched = "";
        }
        this.breadList = matched;
      } else {
        this.breadList = [];
      }
    },
  },
  computed: {
    routes() {
      // console.log(global.antRouter)
      return global.antRouter;
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

/* 滚动条样式隐藏 */
body::-webkit-scrollbar {
  display:none
}

/* .el-main::-webkit-scrollbar {
  display:none
} */

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  color: #2c3e50;
  /* height: 100%; */
  width: 100%;
}

.el-header {
  /* position: relative; */
  height: auto;
  width: 100%;
}

.Site {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh; /*1vh表示浏览器高度的1%，100vh为浏览器高度*/
  /* min-height: 100%;另一种写法 */
  flex-direction: column;
}

/* 顶部进度条自定义颜色 */
#nprogress .bar {
  background: red !important; 
}
</style>



